.ListItem_card__3jm2S {
  display: flex;
  position: relative;
  flex-direction: column;
  height: 350px;
  width: 400px;
  min-width: 250px;
  padding: 1.5rem;
  background: #17141d;
  border-radius: 16px;
  box-shadow: -1rem 0 3rem #000;
  transition: 0.2s;
}
.ListItem_card__3jm2S:focus-within ~ .ListItem_card__3jm2S,
.ListItem_card__3jm2S:hover ~ .ListItem_card__3jm2S {
  transform: translateX(130px);
