}

.ListItem_btn__2hE6k {
  bottom: 0;
  position: absolute;
  background-color: #2f293b;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  border-radius: 12px;
  color: white;
  transform: translate(1%, 50%);
  padding: 8px;
  border: none;
  cursor: pointer;
  font-size: 0.8rem;
  transition: 0.5s;
}

.ListItem_btn__2hE6k:hover {
